
import { defineComponent } from 'vue';

const Button = defineComponent({
  props: {
    label: {
      type: String,
      required: true,
    },
    active: {
      type: Boolean,
      required: false,
    },
    type: {
      type: String,
      required: false,
      validator: (value: string) => value === 'fill',
    },
  },
});

export default Button;
